'use strict';
import path from 'path';
import fs from 'fs';

import test from 'ava';
import Delta from 'quill-delta';

import RtfToDeltaConverter from '..';


function readTestFile(testfile) {
  let testPath = path.join(__dirname, 'data', testfile);
  new Promise((res,rej) => {
    fse.readFile(testPath, (err,data) => {
      if (err) rej(err);
      else res(data);
    });
  });
}

test('test color', async t => {

  let rtfData = await readTestFile('test-color.rtf', {encoding: 'ascii'});
  let converter = new RtfToDeltaConverter();
  let delta = await converter.convert(rtfData);
  console.log(JSON.stringify(delta, null, 2));

  t.is(delta instanceof Delta, 'class check');
  t.is(delta.hasOwnProperty('ops'), 'property check');

});

